"use client";

import { useState } from "react";
import { Search, Plus, Users, ChevronRight, X, Loader2, MessageSquare } from "lucide-react";
import { Avatar } from "@/components/ui/avatar";
import { GradientText } from "@/components/ui/gradient-text";
import { useChat } from "@/contexts/chat-context";
import { useAuth } from "@/contexts/auth-context";
import { ChatService } from "@/services/chat-service";
import { useRouter } from "next/navigation";
import { cn } from "@/lib/utils";

export default function GroupsPage() {
    const { chats } = useChat();
    const { user } = useAuth();
    const router = useRouter();
    const [showCreateModal, setShowCreateModal] = useState(false);
    const [selectedUsers, setSelectedUsers] = useState<string[]>([]);
    const [groupName, setGroupName] = useState("");
    const [loading, setLoading] = useState(false);

    const contacts = chats
        .filter(c => c.type === 'direct')
        .map(c => ({
            id: c.userId,
            name: c.userName,
            avatar: c.userAvatar,
            gender: (c as any).gender
        }));

    const toggleUser = (userId: string) => {
        setSelectedUsers(prev =>
            prev.includes(userId)
                ? prev.filter(id => id !== userId)
                : [...prev, userId]
        );
    };

    const handleCreateGroup = async () => {
        if (!groupName.trim() || selectedUsers.length < 1) return;

        setLoading(true);
        const { data: roomId, error } = await ChatService.createGroupChat(groupName, selectedUsers);

        if (!error && roomId) {
            setShowCreateModal(false);
            router.push(`/chat?roomId=${roomId}`);
        }
        setLoading(false);
    };

    const groupChats = chats.filter(c => c.type === 'group');

    const getNameColor = (gender?: string) => {
        switch (gender?.toLowerCase()) {
            case 'male': return '#00BFFF';
            case 'female': return '#FFB6C1';
            case 'other': return '#FFD700';
            default: return '#FFF';
        }
    };

    return (
        <div className="flex-1 flex flex-col bg-black h-full relative overflow-hidden">
            {/* Background Decor */}
            <div className="absolute top-0 right-0 w-[500px] h-[500px] bg-secondary/5 rounded-full blur-[120px] pointer-events-none" />

            {/* Header */}
            <header className="p-8 pb-4 flex items-center justify-between relative z-10">
                <div className="flex items-center gap-6">
                    <div>
                        <div className="flex items-center gap-4">
                            <GradientText text="Groups" className="text-5xl font-black italic uppercase tracking-tighter" />
                            <div className="w-16 h-16 relative">
                                <img src="/groups_doodle.png" alt="Groups" className="w-full h-full object-contain opacity-80" />
                            </div>
                        </div>
                        <p className="text-white/30 text-xs font-bold uppercase tracking-[0.2em] mt-1">Communities you belong to.</p>
                    </div>
                </div>
                <button
                    onClick={() => setShowCreateModal(true)}
                    className="p-5 bg-gradient-to-br from-primary to-secondary rounded-[24px] shadow-xl shadow-primary/20 hover:scale-110 active:scale-95 transition-all text-white"
                >
                    <Plus className="w-8 h-8" />
                </button>
            </header>

            {/* Content */}
            <div className="flex-1 overflow-y-auto custom-scrollbar p-8 pt-4 relative z-10">
                {groupChats.length === 0 ? (
                    <div className="flex flex-col items-center justify-center p-12 space-y-8 animate-in fade-in zoom-in duration-700">
                        <div className="w-32 h-32 rounded-[40px] bg-white/5 border border-white/10 flex items-center justify-center relative">
                            <div className="absolute inset-0 bg-secondary/10 rounded-[40px] blur-xl animate-pulse" />
                            <Users className="w-12 h-12 text-white/20" />
                        </div>
                        <div className="text-center space-y-4">
                            <h3 className="text-2xl font-black text-white italic uppercase tracking-tight">No group gossips yet</h3>
                            <button
                                onClick={() => setShowCreateModal(true)}
                                className="px-8 py-4 bg-transparent border-2 border-primary/50 hover:border-primary text-primary font-black uppercase italic tracking-widest rounded-2xl transition-all"
                            >
                                Create your first group
                            </button>
                        </div>
                    </div>
                ) : (
                    <div className="space-y-3">
                        {groupChats.map(item => (
                            <div
                                key={item.id}
                                onClick={() => router.push(`/chat?roomId=${item.id}`)}
                                className="group flex items-center gap-4 p-5 rounded-[32px] bg-white/5 border border-white/5 hover:border-white/10 transition-all hover:bg-white/[0.08] cursor-pointer"
                            >
                                <div className="relative">
                                    <Avatar src={item.userAvatar} fallback={item.userName[0]} className="w-14 h-14 rounded-2xl relative z-10" />
                                </div>

                                <div className="flex-1 min-w-0">
                                    <div className="flex items-center justify-between">
                                        <h4 className="text-lg font-black uppercase italic tracking-tight text-white mb-1">
                                            {item.userName}
                                        </h4>
                                        <p className="text-[10px] font-black text-white/20 uppercase tracking-widest">
                                            {new Date(item.lastMessageTime).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' })}
                                        </p>
                                    </div>
                                    <p className="text-[11px] font-bold text-white/40 truncate uppercase tracking-widest">
                                        {item.lastMessage || "Whisper something..."}
                                    </p>
                                </div>

                                <ChevronRight className="w-5 h-5 text-white/20 group-hover:translate-x-1 duration-300" />
                            </div>
                        ))}
                    </div>
                )}
            </div>

            {/* Create Group Modal */}
            {showCreateModal && (
                <div className="fixed inset-0 bg-black/95 backdrop-blur-xl flex items-center justify-center z-50 p-4">
                    <div className="bg-black border border-white/10 w-full max-w-lg rounded-[40px] shadow-2xl overflow-hidden animate-in slide-in-from-bottom-8 duration-500 flex flex-col max-h-[85vh]">
                        <div className="p-8 pb-4 flex items-center justify-between">
                            <GradientText text="New Group" className="text-2xl font-black italic uppercase tracking-tight" />
                            <button onClick={() => setShowCreateModal(false)} className="p-2 text-white/40 hover:text-white transition-colors">
                                <X className="w-8 h-8" />
                            </button>
                        </div>

                        <div className="p-8 pt-4 flex-1 overflow-y-auto custom-scrollbar space-y-8">
                            <div className="space-y-2">
                                <label className="text-[10px] font-black text-white/30 uppercase tracking-widest ml-4">Group Identity</label>
                                <input
                                    value={groupName}
                                    onChange={(e) => setGroupName(e.target.value)}
                                    placeholder="Enter group name..."
                                    className="w-full bg-white/5 border border-white/10 rounded-2xl p-5 text-white font-bold outline-none focus:border-primary/50 transition-all shadow-inner"
                                />
                            </div>

                            <div className="space-y-4">
                                <div className="flex items-center justify-between px-4">
                                    <p className="text-[10px] font-black text-white/20 uppercase tracking-[0.3em]">Add Gossiped Friends</p>
                                    <p className="text-[10px] font-black text-primary uppercase tracking-[0.3em]">{selectedUsers.length} Selected</p>
                                </div>

                                <div className="space-y-2">
                                    {contacts.map(contact => {
                                        const isSelected = selectedUsers.includes(contact.id);
                                        return (
                                            <div
                                                key={contact.id}
                                                onClick={() => toggleUser(contact.id)}
                                                className={cn(
                                                    "flex items-center gap-4 p-4 rounded-[24px] border transition-all cursor-pointer",
                                                    isSelected ? "bg-primary/10 border-primary/20" : "bg-white/5 border-transparent hover:border-white/10"
                                                )}
                                            >
                                                <div className="relative">
                                                    <Avatar src={contact.avatar} fallback={contact.name[0]} className="w-12 h-12 rounded-xl" />
                                                    {isSelected && (
                                                        <div className="absolute -bottom-1 -right-1 w-5 h-5 bg-secondary rounded-full border-2 border-black flex items-center justify-center">
                                                            <div className="w-1.5 h-1.5 bg-black rounded-full" />
                                                        </div>
                                                    )}
                                                </div>
                                                <div className="flex-1">
                                                    <p className="text-[13px] font-black uppercase tracking-tight" style={{ color: getNameColor(contact.gender) }}>{contact.name}</p>
                                                    <p className="text-[9px] font-bold text-white/20 uppercase tracking-widest">Active Partner</p>
                                                </div>
                                                <div className={cn(
                                                    "w-6 h-6 rounded-lg border-2 transition-all flex items-center justify-center",
                                                    isSelected ? "bg-primary border-primary" : "border-white/10"
                                                )}>
                                                    {isSelected && <div className="w-2 h-2 bg-black rounded-full" />}
                                                </div>
                                            </div>
                                        );
                                    })}
                                </div>
                            </div>
                        </div>

                        <div className="p-8 pt-4">
                            <button
                                onClick={handleCreateGroup}
                                disabled={loading || !groupName.trim() || selectedUsers.length < 1}
                                className="w-full py-5 bg-gradient-to-r from-primary to-secondary text-black font-black uppercase italic tracking-[0.2em] rounded-2xl shadow-xl shadow-primary/10 hover:scale-[1.02] transition-all flex items-center justify-center gap-3 active:scale-95 disabled:opacity-50 disabled:hover:scale-100"
                            >
                                {loading ? <Loader2 className="w-5 h-5 animate-spin" /> : <MessageSquare className="w-5 h-5" />}
                                Create Gossip Group
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
